.nav > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
  position: fixed;
  top: 0;
  width: 100%;
}

.nav > ul > li {
  float: left;
}

.nav > ul > li > a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.nav > ul > li > a:hover:not(.active) {
  background-color: #111;
}

.nav > ul > li.active {
  background-color: #4caf50;
}
